import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
//import { AppBar, Box, IconButton, Link } from "@material-ui/core";
import logo from "../a1 (1).jpg";
import logo1 from "../a1 (2).jpg";
import logo2 from "../a1 (3).jpg";
import logo3 from "../a1 (4).jpg";
import { Grid } from "@material-ui/core";
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";
import Hidden from "@material-ui/core/Hidden";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    margin: 25,
  },

  Media: {
    width: "150px",
    height: "150px",
    display: "flex",
    justifyContent: "center",
  },
  card: {
    // padding: theme.spacing(2),
    textAlign: "center",
  },
}));

export default function Mediacard() {
  const classes = useStyles();
  const { push } = useHistory();

  return (
    <div className={classes.root}>
      <h3> Latest TV </h3>
      <Grid
        container
        //spacing={0}
        direction="row"
        alignItems="center"
        justify="center"
        // style={{ minHeight: "50vh" }}
        //spacing={1}
        container
      >
        <Hidden xsDown>
          <Grid item xs={2} sm={3} md={3} lg={3} xl={3}>
            <Card className={classes.root}>
              <CardActionArea>
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <CardMedia
                    className={classes.Media}
                    component="img"
                    justifyContent="center"
                    alt="Contemplative Reptile"
                    //height="70"
                    img
                    src={logo}
                    title="Contemplative Reptile"
                  />
                </div>
                <CardContent className={classes.card}>
                  <Typography gutterBottom variant="h5" component="h2">
                    TCL 49 Inch FHD Smart
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    Rs. 66,900 OFF
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    component={Link}
                    onClick={() => push("/products")}
                    //justify="right"
                  >
                    Shop Now
                  </Button>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        </Hidden>
        <Hidden smDown>
          <Grid item xs={2} sm={3} md={3} lg={3} xl={3}>
            <Card className={classes.root}>
              <CardActionArea>
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <CardMedia
                    className={classes.Media}
                    component="img"
                    justifyContent="center"
                    alt="Contemplative Reptile"
                    //height="70"
                    img
                    src={logo1}
                    title="Contemplative Reptile"
                  />
                </div>
                <CardContent className={classes.card}>
                  <Typography gutterBottom variant="h5" component="h2">
                    TCL 32 Inch FHD Smart
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    Rs. 15,900 OFF
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    component={Link}
                    onClick={() => push("/products")}
                    //justify="right"
                  >
                    Shop Now
                  </Button>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        </Hidden>
        <Hidden mdDown>
          <Grid item xs={2} sm={3} md={3} lg={3} xl={3}>
            <Card className={classes.root}>
              <CardActionArea>
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <CardMedia
                    className={classes.Media}
                    component="img"
                    justifyContent="center"
                    alt="Contemplative Reptile"
                    //height="70"
                    img
                    src={logo2}
                    title="Contemplative Reptile"
                  />
                </div>
                <CardContent className={classes.card}>
                  <Typography gutterBottom variant="h5" component="h2">
                    TCL 50 Inch 4K UHD
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    Rs. 56,900 OFF
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    component={Link}
                    onClick={() => push("/products")}
                    //justify="right"
                  >
                    Shop Now
                  </Button>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        </Hidden>
        <Hidden lgDown>
          <Grid item xs={2} sm={3} md={3} lg={3} xl={3}>
            <Card className={classes.root}>
              <CardActionArea>
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <CardMedia
                    className={classes.Media}
                    component="img"
                    justifyContent="center"
                    alt="Contemplative Reptile"
                    //height="70"
                    img
                    src={logo3}
                    title="Contemplative Reptile"
                  />
                </div>
                <CardContent className={classes.card}>
                  <Typography gutterBottom variant="h5" component="h2">
                    TCL 50 Inch 4K UHD
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    Rs. 4,9000 OFF
                  </Typography>
                  <Button variant="contained" color="primary" href="/products">
                    Shop Now
                  </Button>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        </Hidden>
        <Button
          variant="contained"
          color="primary"
          component={Link}
          onClick={() => push("/products")}
          //justify="right"
        >
          View All Latest HD Televisions
        </Button>
      </Grid>
    </div>
  );
}
