import React from "react";
import { Grid, Button } from "@material-ui/core";

import {
  createMuiTheme,
  withStyles,
  makeStyles,
  ThemeProvider,
} from "@material-ui/core/styles";
//import Button from "@material-ui/core/Button";
import { green, purple } from "@material-ui/core/colors";

const BootstrapButton = withStyles({
  root: {
    boxShadow: "none",
    textTransform: "none",
    fontSize: 16,
    padding: "6px 12px",
    border: "1px solid",
    lineHeight: 1.5,
    //color: theme.palette.getContrastText(purple[500]),
    backgroundColor: purple[500],
    //backgroundColor: "#0063cc",
    borderColor: "#0063cc",
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      backgroundColor: "#0069d9",
      borderColor: "#0062cc",
      boxShadow: "none",
    },
    "&:active": {
      boxShadow: "none",
      backgroundColor: "#0062cc",
      borderColor: "#005cbf",
    },
    "&:focus": {
      boxShadow: "0 0 0 0.2rem rgba(0,123,255,.5)",
    },
  },
})(Button);

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(purple[500]),
    backgroundColor: purple[500],
    "&:hover": {
      backgroundColor: purple[700],
    },
  },
}))(Button);

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  rootpage: {
    marginTop: "20",
  },
}));

const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});

const Notfound = () => {
  const classes = useStyles();

  return (
    <div>
      <Grid container spacing={3}></Grid>
      <Grid container spacing={3}>
        <Grid item xs></Grid>
        <Grid item xs={6}>
          <div id="wrapper">
            <img src="https://i.imgur.com/qIufhof.png" />
            <div id="info">
              <h3>Sorry Oye Mobile could not found your Page</h3>
            </div>
            <ThemeProvider theme={theme}>
              <Button
                fullWidth
                variant="contained"
                color="primary"
                onClick={() => {
                  window.history.back();
                }}
              >
                Go to Back
              </Button>
            </ThemeProvider>
          </div>
        </Grid>
        <Grid item xs></Grid>
      </Grid>
    </div>
  );
};
export default Notfound;
