import React from "react";
import { Button, Grid, TextField, Box } from "@material-ui/core";
//import axios from "axios";
import productService from "./services/ProductsService";
import Admin from "./auth/Admin";

const UpdateProduct = (props) => {
  const [name, setName] = React.useState("");
  const [price, setPrice] = React.useState("");
  const [description, setDescription] = React.useState("");
  const id = props.match.params.id;
  React.useEffect(() => {
    productService.getSingleProduct(id).then((data) => {
      setName(data.name);
      setPrice(data.price);
      setDescription(data.description);
    });
  }, []);
  return (
    <Admin>
      <Box component="span" m={20}></Box>

      <Grid
        style={{ marginTop: "60px", marginLeft: "8px" }}
        container
        spacing={3}
      >
        <Grid item xs></Grid>
        <Grid item xs={6}>
          <h1> Update Product </h1>
          <TextField
            label="name"
            fullWidth
            value={name}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
          <TextField
            label="price"
            fullWidth
            value={price}
            onChange={(e) => {
              setPrice(e.target.value);
            }}
          />
          <TextField
            label="description"
            fullWidth
            value={description}
            onChange={(e) => {
              setDescription(e.target.value);
            }}
          />
        </Grid>
        <Grid item xs></Grid>
      </Grid>
      <Grid container spacing={3}>
        <Grid item xs></Grid>
        <Grid item xs={6}>
          <Button
            style={{ marginLeft: "20px" }}
            variant="contained"
            color="primary"
            onClick={(e) => {
              console.log("Send API Call to Add");
              productService
                .updateProduct(id, { name, price, description })
                .then((data) => {
                  console.log(data);
                  props.history.push("/products");
                })
                .catch((err) => {
                  console.log(err);
                });
            }}
          >
            Update
          </Button>
          {"    "}
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              window.history.back();
            }}
          >
            Go to Back
          </Button>
        </Grid>

        <Grid item xs></Grid>
      </Grid>
    </Admin>
  );
};

export default UpdateProduct;
